<template>
    <div>
       <div class="title">
	       <h2>{{newdata.type}}</h2>
	       <p class="nep">{{newdata.ele}}</p>
	       <i :class="newdata.ico"></i>
       </div>
       <div class="title1">
	       	<h3>{{newdata.name}}</h3>
	       	<div v-for="(i,$index) in newdata.arrdata">
		       	<p class="pna">{{$index+1}}. {{ i.name}}</p>
		       	<p>{{i.text}}</p>
	       	</div>
       </div>
    </div>
</template>

<script>
	 export default {
	 	data:function(){
	 		return {
	 			arr:[
	 			  {
	 			  	type:'',
	 			  	ele:'Online Payment',
	 			  	ico:'glyphicon glyphicon-hand-down',
	 			  	name:'',
	 			  	arrdata:[
	 			  	          {name:'',
	 			  	             text:" "
	 			  	             },
	 			  	             {name:'',
	 			  	             text:""
	 			  	              },
	 			  	              {
	 			  	              name:'',
	 			  	              text:' 800000800100800200800300'
	 			  	              }
	 			  	          ]
	 			  },
	 			  {
	 			  	type:'',
	 			  	ele:'Online Payment',
	 			  	ico:'glyphicon glyphicon-hand-down',
	 			  	name:'',
	 			  	arrdata:[
	 			  	          {name:'',
	 			  	             text:" U"
	 			  	             },
	 			  	             {name:'',
	 			  	             text:""
	 			  	              }
	 			  	          ]
	 			  },
	 			  {
	 			  	type:'',
	 			  	ele:'Online Payment',
	 			  	ico:'glyphicon glyphicon-hand-down',
	 			  	name:'',
	 			  	arrdata:[
	 			  	          {name:'',
	 			  	             text:"2-7"
	 			  	             }
	 			  	          ]
	 			  	}
	 			],
	 			metypes:''
	 		}
	 	},
	 	mounted:function(){
	 		this.$bus.$on('me',(a)=>{
	 		   this.metypes = a
	 		})
	 	},
	 	computed:{
	 		newdata(){
	 			for(var i=0;i<this.arr.length;i++){
	 				if(this.arr[i].type == this.metypes){
	 					return this.arr[i]
	 				}
	 			}
	 		}
	 	}
	 }
</script>

<style>
</style>


